*{
  margin: none;
  padding: none;
}
textarea {   /* for all text* area elements */
  resize: none;
}
h3{
  text-transform: capitalize;
}
body {
  font-family: Helvetica;
}
h3{
  color: #999999;
}

.textarea {
  resize: none;
  width: 400px;
  height: 200px;
  font-size: 17px;
  padding: 20px;
  font-family: system-ui;
  border: none;
  background-color: #070707;
  color: white;
  border-radius: 3px;
}

.textarea::selection {
  background: #141414;
}

.textarea::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

.textarea::-webkit-scrollbar-thumb {
  background-color: white;
  border-radius: 10px;
  border: 3px solid #070707;
}

.textarea::-webkit-scrollbar-thumb:hover {
  background-color: #999999;
}

.textarea:focus {
  outline: none;
}

#pad {
  background: #222d8a;
  display: inline-block;
  padding: 10px;
  padding-top: 4px;
  padding-bottom: 4px;
  border-radius: 10px;
  color: white;
}